import axios from 'axios';

export default {
  async loadProducts({ commit }, params) {
    const response = await axios.get('product', { params });

    if (response.isSuccess()) {
      commit('setProducts', response.getData());
    }

    return response;
  },
  async loadProduct({ commit }, id) {
    const response = await axios.get('product/'+id);

    if (response.isSuccess()) {
      commit('setProduct', response.getData());
    }

    return response;
  },

  async createProduct({ commit }, product) {
    const response = await axios.post('product', product);

    if (response.isSuccess()) {
      commit('setProduct', response.getData());
    }

    return response;
  },

  async editProduct({ commit }, product) {
    const response = await axios.put('product/'+product.id, product);

    if (response.isSuccess()) {
      commit('setProduct', response.getData());
    }

    return response;
  },
};
