<template>
    <v-card>
        <v-container style="min-height: 300px;">
            <v-layout row wrap>
                <v-flex xs9 mb-3>
                    <div class="headline font-weight-bold ">
                        {{productId === 0
                        ? $t('page.product.title_new')
                        : $t('page.product.title_edit')}}
                    </div>
                </v-flex>
                <v-flex xs12 mb-3>
                    <v-divider></v-divider>
                </v-flex>
                <v-flex xs12>
                    <v-form data-vv-scope="product" @submit.prevent="save">
                        <v-layout row wrap>
                            <v-flex md5 mr-3>
                                <v-text-field id="name"
                                              name="name"
                                              :label="$t('label.name')"
                                              v-model="form.name"
                                              v-validate="'required|min:3'"
                                              :disabled="!form.is_owner"
                                              :error-messages="errors.first('product.name')"
                                              @keyup.enter="save"
                                >
                                </v-text-field>
                            </v-flex>
                            <v-flex md5 mr-3>
                                <v-text-field id="sku"
                                              name="sku"
                                              :label="$t('label.sku')"
                                              v-model="form.sku"
                                              :disabled="productId !== 0 || !form.is_owner"
                                              v-validate="'required'"
                                              :error-messages="errors.first('product.sku')"
                                              @keyup.enter="save"
                                >
                                </v-text-field>
                            </v-flex>
                        </v-layout>
                        <v-layout row wrap>
                            <v-flex md5 mr-3>
                                <v-text-field id="price"
                                              name="price"
                                              :label="$t('label.price')"
                                              v-model.lazy="form.price"
                                              :disabled="!form.is_owner"
                                              v-mask-decimal.br="2"
                                              v-validate="'required|min:0'"
                                              :error-messages="errors.first('product.price')"
                                              @keyup.enter="save"
                                >
                                </v-text-field>
                            </v-flex>
                            <v-flex md5 mr-3>
                                <v-text-field id="description"
                                              name="description"
                                              :label="$t('label.description')"
                                              :disabled="!form.is_owner"
                                              v-model="form.description"
                                              v-validate="'required|min:3'"
                                              :error-messages="errors.first('product.description')"
                                              @keyup.enter="save"
                                >
                                </v-text-field>
                            </v-flex>
                        </v-layout>
                        <v-btn color="error"
                               :to="{name: 'products'}"
                               outline>
                            {{$t('btn.cancel')}}
                        </v-btn>
                        <v-btn color="primary"
                               @click="save"
                               :loading="loading"
                               :disabled="loading || !form.is_owner">
                            {{$t('btn.save')}}
                        </v-btn>
                    </v-form>

                </v-flex>
            </v-layout>
        </v-container>
    </v-card>
</template>

<script>
  import { mapGetters } from 'vuex';

  export default {
    name: 'Products',
    computed: {
      ...mapGetters({
        product: 'products/getProduct',
      }),
    },
    data() {
      return {
        loading: false,
        productId: 0,
        form: {
          name: '',
          description: '',
          sku: '',
          price: 0,
          is_owner: false
        },
        money: {
          decimal: ',',
          thousands: '.',
          prefix: 'R$ ',
          precision: 2,
          masked: false /* doesn't work with directive */
        }
      };
    },
    created() {
      this.productId = this.$router.currentRoute.params.id;

      if (this.productId) {
        this.loadProduct();
      }
    },
    methods: {
      async loadProduct() {
        this.loading = true;
        let response = await this.$store.dispatch('products/loadProduct', this.productId);
        this.loading = false;

        if (response.isFailed()) {
          return this.$store.dispatch('setToast', { text: response.getMessage() });
        }

        this.form = {...this.product};
      },
      async save() {
        let response;
        this.loading = true;

        let form = {...this.form};

        form.price = parseFloat(form.price.replace('.', '').replace(',', '.'));

        if(this.productId !== 0){

          delete form.sku;

          response = await this.$store.dispatch('products/editProduct', form);
        }else{
          response = await this.$store.dispatch('products/createProduct', form);
        }

        this.loading = false;

        this.handleResponse(response, 'product', () => {
          this.$router.push({name: 'products'});
          this.$store.dispatch('setToast', { text: this.$t('page.products.product_added_success') });
        });

        if(response.isFailed()){
          this.$store.dispatch('setToast', { text: response.getMessage() });
        }
      },
    },
  };
</script>

<style scoped>
    .btn-full.v-btn {
        width: 100%;
        padding: 0;
    }

    .tipo {
        font-family: Montserrat, Roboto, sans-serif;
        font-size: 15px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        color: #4a4a4a;
    }

</style>
