<template>
    <v-card>
        <v-btn
                dark
                fab
                fixed
                bottom
                right
                @click="newProduct()"
        >
            <v-icon>add</v-icon>
        </v-btn>
        <v-container style="min-height: 300px;" >

            <v-layout row wrap>

                <v-flex xs9 mb-3>
                    <div class="headline font-weight-bold ">{{$t('page.products.title')}}</div>
                </v-flex>
                <v-flex xs12 mb-3>
                    <v-divider></v-divider>
                </v-flex>
                <v-flex xs3 pr-1>
                    <v-text-field append-icon="search"
                                  name="search"
                                  :label="$t('label.search')"
                                  v-model="query.search"
                                  type="text">
                    </v-text-field>
                </v-flex>
                <v-flex xs12>
                    <v-data-table
                            :headers="headers"
                            :items="products"
                            :pagination.sync="pagination"
                            class="elevation-1"
                            :loading="loading"
                            :total-items="query.limit"
                            :no-data-text="$t('table.no_results')"
                            :no-results-text="$t('table.no_results')"
                            hide-actions
                    >
                        <template slot="items" slot-scope="props">
                            <td>
                                {{ props.item.name }}
                            </td>

                            <td class="align-content-center justify-center">
                                {{ props.item.price }}
                            </td>

                            <td>
                                <v-btn flat icon color="primary" @click="showProduct(props.item)">
                                    <v-icon>{{props.item.is_owner ? 'edit' : 'remove_red_eye'}}</v-icon>
                                </v-btn>
                            </td>
                            <td>
                                <v-btn v-if="!inCart(props.item.id)" flat icon color="success" @click="addProduct(props.item)">
                                    <v-icon>add_shopping_cart</v-icon>
                                </v-btn>
                            </td>
                        </template>
                    </v-data-table>
                    <div class="text-xs-center pt-2">
                        <v-pagination v-model="pagination.page" :length="pages"></v-pagination>
                    </div>
                </v-flex>
            </v-layout>
        </v-container>
    </v-card>
</template>

<script>
import { mapGetters } from 'vuex';

import _ from 'lodash';

export default {
  name: 'Products',
  computed: {
    ...mapGetters({
      products: 'products/getProducts',
      productsIdsInCart: 'cart/getProductsIds'
    }),
  },
  data() {
    return {
      loading: false,
      debounceSearch: _.debounce(this.sendSearch, 300),
      pages: 1,
      totalItems: 0,
      pagination: {
        descending: false,
        page: 1,
        sortBy: 'name',
      },
      query: {
        search: '',
        limit: 15,
      },
      headers: [
        {
          text: this.$t('page.products.table.headers.name'),
          value: 'name',
        },
        {
          text: this.$t('page.products.table.headers.price'),
          value: 'price',
        },
        {
          sortable: false,
        },
        {
          sortable: false,
        },
      ],
    };
  },
  methods: {
    inCart(id){
      return this.productsIdsInCart.indexOf(id) !== -1;
    },
    showProduct(product) {
      return this.$router.push({
        name: 'edit-product',
        params: { id: product.id },
      });
    },
    newProduct(){
      return this.$router.push({
        name: 'new-product'
      })
    },
    async addProduct(product){
      let response = await this.$store.dispatch('cart/addProduct', product);

      if(response.isFailed()){
        this.$store.dispatch('setToast', {text: response.getMessage()})
      }else{
        this.$store.dispatch('setToast', {text: this.$t('page.products.product_added_success')})
      }
    },
    async sendSearch() {
      const query = {
        sort: this.pagination.sortBy,
        page: this.pagination.page,
        limit: this.query.limit,
        search: this.query.search,
        order: this.pagination.descending ? 'desc' : 'asc',
      };

      this.loading = true;

      const response = await this.$store.dispatch('products/loadProducts', query);

      this.pages = response.getLastPage();
      this.pagination.page = response.getCurrentPage();
      this.totalItems = response.getTotalItens();
      this.loading = false;
    },


  },
  watch: {
    query: {
      handler() {
        this.debounceSearch();
      },
      deep: true,
    },
    pagination: {
      handler() {
        this.debounceSearch();
      },
      deep: true,
    },
  },
};
</script>

<style scoped>
    .btn-full.v-btn {
        width: 100%;
        padding: 0;
    }

    .tipo {
        font-family: Montserrat, Roboto, sans-serif;
        font-size: 15px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        color: #4a4a4a;
    }

</style>
