import axios from 'axios';

export default {
  async addProduct({ commit, state }, product) {

    let response;

    if (state.cart.id === 0) {
      let data = {
        products: [
          product.id
        ]
      };
      response = await axios.post('order', data);
    } else {
      let data = {
        products_added: [
          product.id
        ]
      };
      response = await axios.put('order/' + state.cart.id, data);
    }

    if (response.isSuccess()) {
      commit('setCart', response.getData());
    }

    return response;
  },

  async removeProduct({commit, state}, productId){
      if(state.cart.id === 0){
        return false;
      }

    let data = {
      products_removed: [
        productId
      ]
    };
    const response = await axios.put('order/' + state.cart.id, data);

    if (response.isSuccess()) {
      commit('setCart', response.getData());
    }

    return response;
  },

  async finishCart({commit, state}){
    const response = await axios.post('order/' + state.cart.id + '/finish', {});

    if (response.isSuccess()) {
      commit('resetCart');
    }

    return response;
  },

  async loadCart({ commit }) {
    const response = await axios.get('me/order');

    if (response.isSuccess()) {
      commit('setCart', response.getData());
    }

    return response;
  }
};

