<template>
    <v-dialog
            v-model="value"
            fullscreen
            hide-overlay
            transition="dialog-bottom-transition"
            scrollable
    >
        <v-card tile>
            <v-toolbar card dark color="primary">
                <v-btn icon dark @click.native="close">
                    <v-icon>close</v-icon>
                </v-btn>
                <v-toolbar-title>{{$t('page.cart.title')}}</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-toolbar-items v-if="products.length > 0">
                    <v-btn dark flat @click.native="finishCart" :disabled="loading">{{$t('btn.finish')}}</v-btn>
                </v-toolbar-items>
            </v-toolbar>
            <v-card-text>
                <v-list three-line subheader v-if="products.length > 0">
                    <v-list-tile v-for="product in products" :key="product.id" v-if="products.length > 0">
                        <v-list-tile-content>
                            <v-list-tile-title>
                                {{product.name}}
                            </v-list-tile-title>
                            <v-list-tile-sub-title>
                                {{product.price}}
                            </v-list-tile-sub-title>
                        </v-list-tile-content>

                        <v-list-tile-action>
                            <v-btn :disabled="loading" outline fab color="error" @click="removeProduct(product.id)">
                                <v-icon>remove_shopping_cart</v-icon>
                            </v-btn>
                        </v-list-tile-action>

                    </v-list-tile>

                </v-list>
                <v-list-tile v-else>
                    <v-list-tile-content>
                        <v-list-tile-title> {{$t('page.cart.no_products')}}</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
                <v-divider></v-divider>
            </v-card-text>
        </v-card>
    </v-dialog>
</template>

<script>
  import { mapGetters } from 'vuex';

  export default {
    name: 'CartDialog',
    props: ['value'],
    data() {
      return {
        loading: false
      };
    },
    computed: {
      ...mapGetters({
        products: 'cart/getProducts',
        cart: 'cart/getCart'
      })
    },
    methods: {
      async removeProduct(id) {
        this.loading = true;

        let response = await this.$store.dispatch('cart/removeProduct', id);

        this.loading = false;

        if (response.isSuccess()) {
          await this.$store.dispatch('setToast', { text: this.$t('page.cart.product_removed_success') });
        } else {
          await this.$store.dispatch('setToast', { text: response.getMessage() });
        }
      },
      async finishCart() {
        this.loading = true;

        let response = await this.$store.dispatch('cart/finishCart');

        this.loading = false;

        if (response.isSuccess()) {
          await this.$store.dispatch('setToast', { text: this.$t('page.cart.finished_success') });
        } else {
          await this.$store.dispatch('setToast', { text: response.getMessage() });
        }

        this.close();
      },
      close() {
        this.$emit('input', false);
      }
    }
  };

</script>

