import BasePage from '@/views/Painel/BasePainel.vue';
import Orders from '@/views/Painel/Orders.vue';
import Products from '@/views/Painel/Products.vue';
import Product from '@/views/Painel/Product.vue';

import Login from '@/views/Auth/Login.vue';
import SignUp from '@/views/Auth/SignUp.vue';

export default [
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {
      auth: false,
    },
  },
  {
    path: '/sign-up',
    name: 'sign-up',
    component: SignUp,
    meta: {
      auth: false,
    },
  },

  {
    path: '/',
    component: BasePage,
    meta: {
      auth: true,
    },
    children: [
      {
        path: '/products',
        name: 'products',
        component: Products,
      },
      {
        path: '/product/:id',
        name: 'edit-product',
        component: Product,
      },
      {
        path: '/product',
        name: 'new-product',
        component: Product,
      },
      {
        path: '/orders',
        name: 'orders',
        component: Orders,
      },
    ],
  },
];

