export default {
  addProduct(state, product) {
    state.cart.products.push(product);
  },
  removeProduct(state, productId) {
    productId = parseInt(productId);
    let index = state.cart.products.findIndex((product) => parseInt(product.id) === productId);
    if (index !== -1) {
      state.cart.splice(index, 1);
    }
  },
  setCart(state, cart) {
    if (cart == null) {
      state.cart = {
        id: 0,
        total: 0,
        products: [],
      };

      return;
    }

    state.cart = cart;
  },
  resetCart(state) {
    state.cart = {
      id: 0,
      total: 0,
      products: [],
    };
  },
};
