<template>
    <v-card>
        <v-container style="min-height: 300px;" >
            <v-layout row wrap>
                <v-flex xs9 mb-3>
                    <div class="headline font-weight-bold ">{{$t('page.orders.title')}}</div>
                </v-flex>
                <v-flex xs12 mb-3>
                    <v-divider></v-divider>
                </v-flex>
                <v-flex md3>
                    <v-select
                            v-model="query.status"
                            :items="status"
                            :label="$t('label.status')"
                    ></v-select>
                </v-flex>
                <v-flex xs12>
                    <v-data-table
                            :headers="headers"
                            :items="orders"
                            :pagination.sync="pagination"
                            class="elevation-1"
                            :loading="loading"
                            :total-items="query.limit"
                            :no-data-text="$t('table.no_results')"
                            :no-results-text="$t('table.no_results')"
                            hide-actions
                    >
                        <template slot="items" slot-scope="props">
                            <td class="text-xs-center">
                                {{ formatDate(props.item.created_at) }}
                            </td>

                            <td class="text-xs-center">
                                R$ {{ props.item.total }}
                            </td>

                            <td class="text-xs-center">
                                {{ props.item.products_count }}
                            </td>

                            <td class="text-xs-center">
                                {{ $t('status.' + props.item.status_display.toLowerCase()) }}
                            </td>

                            <td>
                                <v-btn flat icon color="primary" @click="showOrder(props.item)">
                                    <v-icon>remove_red_eye</v-icon>
                                </v-btn>
                            </td>
                        </template>
                    </v-data-table>
                    <div class="text-xs-center pt-2">
                        <v-pagination v-model="pagination.page" :length="pages"></v-pagination>
                    </div>
                </v-flex>
            </v-layout>
        </v-container>
        <order-dialog v-model="dialog.open" :order="dialog.order"></order-dialog>
    </v-card>
</template>

<script>
import { mapGetters } from 'vuex';

import _ from 'lodash';
import moment from 'moment';
import OrderDialog from '@/components/OrderDialog';

export default {
  name: 'Orders',
  components: { OrderDialog },
  computed: {
    ...mapGetters({
      orders: 'orders/getOrders',
      status: 'getAllStatus'
    }),
  },
  data() {
    return {
      dialog: {
        open: false,
        order: {
          products: []
        }
      },
      loading: false,
      debounceSearch: _.debounce(this.sendSearch, 300),
      pages: 1,
      totalItems: 0,
      pagination: {
        descending: false,
        page: 1,
        sortBy: 'created_at',
      },
      query: {
        search: '',
        limit: 15,
        status: 0
      },
      headers: [
        {
          text: this.$t('page.orders.table.headers.created_at'),
          value: 'created_at',
          align: 'center'
        },
        {
          text: this.$t('page.orders.table.headers.total'),
          value: 'total',
          align: 'center'
        },
        {
          text: this.$t('page.orders.table.headers.quantity'),
          value: 'products_count',
          align: 'center'
        },
        {
          text: this.$t('page.orders.table.headers.status'),
          value: 'status',
          align: 'center'
        },
        {
          text: '',
          value: 'actions',
          sortable: false,
        },
      ],
    };
  },
  methods: {
    formatDate(date){
      return moment(date).format('DD/MM/YYYY H:mm:ss')
    },
    showOrder(order) {
     this.dialog.order = order;
     this.dialog.open = true;
    },
    async sendSearch() {
      const query = {
        sort: this.pagination.sortBy,
        page: this.pagination.page,
        limit: this.query.limit,
        search: this.query.search,
        order: this.pagination.descending ? 'desc' : 'asc',
        status: this.query.status
      };

      this.loading = true;

      const response = await this.$store.dispatch('orders/loadOrders', query);

      this.pages = response.getLastPage();
      this.pagination.page = response.getCurrentPage();
      this.totalItems = response.getTotalItens();
      this.loading = false;
    },


  },
  watch: {
    query: {
      handler() {
        this.debounceSearch();
      },
      deep: true,
    },
    pagination: {
      handler() {
        this.debounceSearch();
      },
      deep: true,
    },
  },
};
</script>

<style scoped>
    .btn-full.v-btn {
        width: 100%;
        padding: 0;
    }

    .tipo {
        font-family: Montserrat, Roboto, sans-serif;
        font-size: 15px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        color: #4a4a4a;
    }

</style>
